app-header md-sidenav.md-warn {
  background-color: #009688;
}

app-header .md-subheader .md-subheader-inner {
  padding: 0;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools {
  height: inherit;
  max-height: 110px;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools.right {
  padding: 0;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools .logo {
  margin: 0 10px;
  width: 40px;
  height: 60px;
}

@media (max-width: 567px) {
  app-header .md-subheader .md-subheader-inner .md-toolbar-tools .logo {
    height: 40px;
  }
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools .md-title {
  font-size: 40px;
}

@media (max-width: 567px) {
  app-header .md-subheader .md-subheader-inner .md-toolbar-tools .md-title {
    font-size: 20px;
  }
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools > .md-button:first-child {
  margin-left: 0;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools > .md-button:last-child {
  margin-right: 0;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools form {
  margin-bottom: 0;
  margin-top: 17px;
}

app-header .md-subheader .md-subheader-inner .md-toolbar-tools form md-input-container {
  margin: 0;
}
