md-content.warpper-login {
  background-image: url("../img/bg-login.jpg");
  background-size: cover;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

md-content.warpper-login section {
  height: 100%;
}

md-content.warpper-login .md-title > small {
  font-weight: 400;
  font-size: 75%;
}

md-content.warpper-login .font-small span, md-content.warpper-login .font-small button {
  font-size: 12px;
  text-transform: inherit;
}

md-content.warpper-login .form-login {
  position: relative;
  width: 416px;
  min-width: 416px;
  max-width: 416px;
  height: 100%;
  background: #FFF;
  overflow: hidden;
}

@media screen and (max-width: 960px) {
  md-content.warpper-login .form-login {
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    overflow: auto;
  }
}

md-content.warpper-login .form-login .wrapper {
  padding: 15% 10%;
}

md-content.warpper-login .form-login .wrapper img {
  width: 40%;
  margin: auto;
  margin-bottom: 5%;
}
