app-grid-list > md-card:first-child {
  background-color: #009688;
}

app-grid-list md-grid-tile {
  overflow: hidden;
  transition: all 400ms ease-out 50ms;
}

app-grid-list md-grid-tile figure {
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
  align-items: flex-end;
  -webkit-align-content: flex-end;
  align-content: flex-end;
}

app-grid-list md-grid-tile figure md-grid-tile-header {
  background: black;
}

app-grid-list md-grid-tile figure md-grid-tile-header figcaption {
  text-transform: uppercase;
  text-align: center;
  width: 100%;
}

@media (max-width: 1367px) {
  app-grid-list md-grid-tile figure md-grid-tile-header h3 {
    font-size: 13px;
  }
}

@media (max-width: 360px) {
  app-grid-list md-grid-tile figure md-grid-tile-header h3 {
    font-size: 11px;
  }
}

app-grid-list md-grid-tile figure md-grid-tile-footer {
  opacity: 0.5;
  height: 18%;
}

app-grid-list md-grid-tile img {
  width: 100%;
}

app-grid-list md-tabs-canvas.md-center-tabs md-pagination-wrapper .md-tab {
  border-radius: 24px;
  background-color: rgba(0, 0, 0, 0.24);
  width: 48px;
  padding: 12px 0;
  font-size: 16px;
  margin: 0 0.7%;
}

app-grid-list md-tabs-canvas.md-center-tabs md-pagination-wrapper .md-tab .md-ripple-container {
  border-radius: 24px;
}

app-grid-list md-tabs-canvas.md-center-tabs md-pagination-wrapper .md-tab.md-active {
  background-color: #009688;
  color: #ffffff;
}

app-grid-list md-tabs-canvas.md-center-tabs md-pagination-wrapper md-ink-bar {
  background-color: #009688;
  height: 48px;
  width: 48px;
  border-radius: 24px;
  z-index: -1;
}

app-grid-list md-tabs-canvas.md-center-tabs md-pagination-wrapper .md-button.md-icon-button {
  width: 48px;
  height: 48px;
}
