md-list.app-menu-mega {
  background-color: #009688;
  color: white;
}

md-list.app-menu-mega md-list-item, md-list.app-menu-mega md-list-item .md-list-item-inner {
  min-height: 40px;
}

md-list.app-menu-mega md-list-item:before, md-list.app-menu-mega md-list-item .md-list-item-inner:before {
  min-height: 40px;
}

md-list.app-menu-mega md-list-item .material-icons, md-list.app-menu-mega md-list-item .md-list-item-inner .material-icons {
  color: white;
}

md-menu-content.menu-mega {
  max-height: inherit;
  min-width: 680px;
  padding: 16px 0;
}

md-menu-content.menu-mega md-toolbar {
  min-height: 40px;
}

md-menu-content.menu-mega .md-toolbar-tools {
  font-size: 16px;
  height: inherit;
  max-height: inherit;
}

md-menu-content.menu-mega .md-toolbar-tools .md-avatar {
  margin-top: 8px;
  margin-bottom: 8px;
  margin-right: 16px;
  border-radius: 50%;
  box-sizing: content-box;
  -webkit-box-flex: 0;
  -webkit-flex: none;
  flex: none;
  width: 30px;
  height: 30px;
}

md-menu-content.menu-mega md-menu-item {
  height: inherit;
}

md-menu-content.menu-mega md-menu-item md-list-item._md-button-wrap {
  height: 30px;
  min-height: inherit;
  font-size: 14px;
}

md-menu-content.menu-mega md-menu-item md-list-item._md-button-wrap .md-no-style.md-button {
  height: 30px;
  min-height: inherit;
}
