app-slideshow {
  min-height: 28%;
  position: relative;
  overflow: hidden;
}

app-slideshow md-tabs[md-align-tabs="bottom"] {
  padding-bottom: 15px;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-tabs-content-wrapper {
  height: 100%;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-tabs-content-wrapper md-tab-content {
  height: 100%;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-tabs-content-wrapper md-tab-content .background-image {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-tabs-wrapper {
  height: 15px;
  bottom: 25px;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-tabs-wrapper md-tabs-canvas {
  height: 15px;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-pagination-wrapper.md-center-tabs {
  margin: 0 auto;
  height: 15px;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-pagination-wrapper.md-center-tabs .md-tab {
  padding: 0;
  width: 15px;
  font-size: 0;
  background-color: #b3b3b3;
  border-radius: 8px;
  margin: 0 4px;
}

app-slideshow md-tabs[md-align-tabs="bottom"] md-pagination-wrapper.md-center-tabs md-ink-bar {
  height: 15px;
  width: 15px;
  border-radius: 10px;
}
